
#show-blogs{
	max-width: 800px;
	margin: 0 auto;
}
.single-blog{
	padding: 20px;
	margin: 20px 0;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	background: #eee;
	border: 1px dotted #aaa;
}
#show-blogs a{
	color: #444;
	text-decoration: none;
}
input[type="text"]{
	padding: 8px;
	width: 100%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}



